"use client";

import { useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import { useEffect } from "react";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogFooter,
  DialogClose,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { format } from "date-fns"
import { Calendar as CalendarIcon } from "lucide-react"
 
import { cn } from "@/lib/utils"
import { Calendar } from "@/components/ui/calendar"
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover"
export default function DashboardPage() {
  const { data: session, status } = useSession();
  const router = useRouter();

  useEffect(() => {
    if (status === "unauthenticated" || (status === "loading" && !session)) {
      router.replace("/login");
    }
  }, [status, session, router]);

  if (status === "loading") return <p>Carregando...</p>;

  if (!session) return null;

  return (
    <div className="flex overflow-hidden min-h-screen">
      <div className="flex flex-col flex-1">
        <h1 className="py-4 pl-2 text-4xl font-bold tracking-tight text-balance">
          Dashboard
        </h1>
        <main className="flex justify-center flex-1">
          <div className="flex pt-10 gap-2">
            <Dialog>
              <form>
                <DialogTrigger asChild>
                  <Button variant="outline">Adicionar Despesa</Button>
                </DialogTrigger>
                <DialogContent className="sm:max-w-[425px]">
                  <DialogHeader>
                    <DialogTitle>Adicionar nova despesa</DialogTitle>
                    <DialogDescription>
                      Adicione uma nova despesa preenchendo os campos abaixo.
                    </DialogDescription>
                  </DialogHeader>
                  <div className="grid gap-4">
                    <div className="grid gap-3">
                      <Label htmlFor="valor">Valor</Label>
                      <Input
                        id="valor"
                        name="valor"
                      />
                    </div>
                    <div className="grid gap-3">
                      <Label htmlFor="username-1">Categoria</Label>
                      <Select>
                        <SelectTrigger>
                          <SelectValue placeholder="Selecione uma categoria" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="alimentacao">Alimentação</SelectItem>
                          <SelectItem value="transporte">Transporte</SelectItem>
                          <SelectItem value="saude">Saúde</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div className="grid gap-3">
                      <Label htmlFor="valor">Data</Label>
                      <Input
                        id="valor"
                        name="valor"
                      />
                    </div>
                  </div>
                  <DialogFooter>
                    <DialogClose asChild>
                      <Button variant="outline">Cancel</Button>
                    </DialogClose>
                    <Button type="submit">Save changes</Button>
                  </DialogFooter>
                </DialogContent>
              </form>
            </Dialog>
          </div>
        </main>
      </div>
    </div>
  );
}
